.wpp-container-payment {
  background-color: #fff;
  border-radius: 10px;
  padding: 30px 30px 120px;
}

.wpp-container-payment ::selection {
  color: #fff;
  background-color: #65a300;
}

.wpp-container-payment .woocommerce-help-tip {
  color: #65a300;
}

.wpp-container-payment .submit {
  float: right;
}

.wpp-container-payment .submit button {
  height: 40px;
  background-color: #65a300;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 0 30px;
  box-shadow: 2px 2px 2px 1px #0003;
}

.wpp-container-payment .submit button:hover {
  opacity: .9;
  background-color: #65a300 !important;
}

.wpp-container-payment .submit button::selection, .wpp-container-payment .submit button:focus {
  color: #fff;
  background-color: #65a300;
  border: none !important;
}

.wpp-container-payment .nav-tab-wrapper a {
  background-color: #fff;
  border-radius: 10px 10px 0 0;
  padding: 5px 10px;
}

#woocommerce_wc-pagarme-credit_installments_config {
  height: 30px;
  max-width: 150px;
  color: #fff;
  background-color: #65a300;
  border: none;
  border-radius: 5px;
  box-shadow: 2px 2px 2px 1px #0003;
}

#woocommerce_wc-pagarme-credit_installments_config:hover {
  cursor: pointer;
  opacity: .9;
}

.wpp-darker {
  z-index: 9998;
  width: 100%;
  height: 100%;
  background-color: #00000080;
  justify-content: center;
  align-items: center;
  padding: 0 30px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.wpp-darker .wpp-popup {
  height: -moz-fit-content;
  height: fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 450px;
  background: #fff;
  border-radius: 10px;
  padding: 60px 0;
}

.wpp-darker .wpp-popup .wpp-popup-icon {
  color: #65a300;
  justify-content: center;
  align-items: center;
  font-size: 60px;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-circle-xmark {
  color: #f66;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-circle-check {
  color: #65a300;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-triangle-exclamation {
  color: orange;
}

.wpp-darker .wpp-popup .wpp-popup-icon .fa-spinner {
  color: #74ff33;
  animation: 2s ease-in-out infinite dash;
}

.wpp-darker .wpp-popup .wpp-popup-title {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 30px;
  font-weight: 600;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-message {
  text-align: center;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: 10px;
  font-size: 16px;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-button {
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  font-size: 18px;
  display: flex;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn {
  width: -moz-fit-content;
  width: fit-content;
  height: 40px;
  border: none;
  border-radius: 5px;
  margin: 0 5px;
  padding: 10px 30px;
  font-size: 14px;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-failed {
  color: #fff;
  background: #e72e35;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-failed:hover {
  cursor: pointer;
  background: #f66;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-success {
  color: #fff;
  background: #50c6e9;
}

.wpp-darker .wpp-popup .wpp-popup-button .btn-success:hover {
  cursor: pointer;
  background: #23b7e3;
}

@media (max-width: 900px) {
  .wpp-darker {
    padding: 300px 30px 0 0;
  }
}

@keyframes dash {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#wpp-installments-container {
